/* Sticky layout fix */
html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.app-container > *:last-child {
  margin-top: auto;
}

body {
  margin: 0;
  padding: 0;
  background-color: #f9f9f9;
}


main {
  flex-grow: 1;
}


